@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  background: use(--secondary-main);
  overflow: hidden;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  display: flex;
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameWrapper {
  width: 100%;
  height: 400px;
  position: relative;
}

.gameCell {
  height: 100%;
  width: 70%;
}

.gameFooter {
  padding: 10px;
}
